import React, { useState, useRef, useCallback } from "react";
import Carousel from 'react-bootstrap/Carousel'
import 'bootstrap/dist/css/bootstrap.min.css';
import { Room } from "../generated/graphql";
import '../assets/scss/room.scss';
type Props = {
    room: Room
};

const RoomdetailPicture: React.FunctionComponent<Props> = ({
    room
}) => {
    const [index, setIndex] = useState(0);
    const [direction, setDirection] = useState(null);

    const handleSelect = (selectedIndex: any, e: any) => {
        setIndex(selectedIndex);
        setDirection(e.direction);
    };
    return (
        <div style={{ width: '100%', height: '100%' }}>
            <Carousel activeIndex={index} onSelect={handleSelect}>
                <Carousel.Item>
                    <div>
                        <div style={ { width: '100%', height: '260px' ,backgroundColor: 'black', backgroundImage: 'url("https://t1.daumcdn.net/cfile/tistory/998657375A4FA1BC0E")' } }></div>
                    </div>
                </Carousel.Item>
                <Carousel.Item>
                    <div>
                        <div>222</div>
                    </div>
                </Carousel.Item>
            </Carousel>
        </div>
    );
}

export default RoomdetailPicture;